.App {
  text-align:center;
}
body{
  display: grid;
  min-height: 10vh;
  grid-template-rows: auto 1fr auto;
  background-color: #D7BDE2;
}
img {
  border: 5px ;
  width: 900px;
  height: 500px;
}

.App-header {
  background-color: #47f08592;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}

.logitofooter{
  width: 70px;
  height: 70px;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #283747 ;
  padding: 20px;
  position: absolute;
  width:100%;
}
.footer-logo {
  width: 100px;
  height: 100px;
  border: #61dafb solid;
}
.footer-info {
  font-family: Arial, sans-serif;
  font-size: 14px;
  color: white;
}
.footer-name {
  font-weight: bold;
  margin-bottom: 10px;
}
.footer-address, .footer-phone {
  margin-bottom: 5px;
}
.footer-autor{
  margin-bottom: 5px;
  color: white ;
}
.textito{
  max-width: 80vw;
  margin: 10px auto;
  padding: 20px; 
}
h1 {
  padding-top: 40px;
  color: #17a2b8; 
  font-size: 3.5em; 
  text-align: center; 
  text-transform: uppercase; 
  font-weight: bold; 
  letter-spacing: 4px; 
  margin-bottom: 30px; 
  text-shadow: 3px 3px 6px rgba(23, 162, 184, 0.7); 
  text-align: center;
  color: #333;
  
}
.computadores{
  width: 600px;
}

.columna1{
  padding: 10px;
  margin: 0px;
}

.contenedor {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #D7BDE2;
  padding: 30px;
}
.container{
  font-family: Arial, sans-serif;
  
  display: flex;
  justify-content: center;
  
}
.left-column{
  max-width: 100%;
  height: auto;

}
.right-column {
  flex: 1;
  min-width: 300px;

}
h2{
  font-size: 38px;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: rgb(229, 245, 10);
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.847);
  background-color: rgba(10, 6, 65, 0.68);
  border: 4px solid rgb(29, 0, 35);
  border-radius: 10px;
  padding: 10 px;
  margin: 20px;
}
ul {
  list-style-type: square;
  margin-left: 20px;
}
